@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "roboto-condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "roboto-condensed";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "roboto";
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "pt-mono";
  font-display: swap;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "pt-mono", "Arial", monospace;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-block;
  padding: 10px 20px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  font-family: "roboto-condensed", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background-color: #00b2ff;
  border: 1px solid #00b2ff;
  border-radius: 3px;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}
.btn--red {
  background-color: #ff1553;
}
.btn--transparent {
  background-color: transparent;
  border: 1px solid #f2f5f7;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.main-header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 3;
  padding-bottom: 8px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}

.main-nav {
  display: flex;
  align-items: center;
  padding-top: 27px;
  padding-bottom: 27px;
}
@media (max-width: 1023px) {
  .main-nav {
    padding-top: 20px;
    padding-bottom: 20px;
    justify-content: space-between;
    align-items: flex-start;
  }
}
@media (max-width: 767px) {
  .main-nav {
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.main-nav__logo {
  display: block;
  width: 215px;
  height: 55px;
}
@media (max-width: 767px) {
  .main-nav__logo {
    width: 35px;
    height: 35px;
  }
}

.main-nav__list {
  display: grid;
  gap: 10px 127px;
  grid-template-columns: auto auto;
  align-items: center;
  margin: 0 auto;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .main-nav__list {
    margin: 0;
    gap: 0 0;
    column-gap: 20px;
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .main-nav__list {
    margin-right: 10px;
    margin-top: -5px;
  }
}

.main-nav__image {
  display: block;
  width: 100%;
  height: 100%;
}

.main-nav__link {
  display: grid;
  align-items: center;
  font-size: 16px;
  line-height: 18px;
  color: #f2f5f7;
  gap: 12px;
  grid-template-columns: repeat(2, auto);
}
@media (max-width: 1023px) {
  .main-nav__link {
    font-size: 0;
    gap: 0;
    padding: 10px;
  }
}

.main-nav__link svg {
  background-color: #041827;
}

.main-nav__btn {
  width: 160px;
  margin-right: 60px;
  padding: 10px 5px;
}
@media (max-width: 1023px) {
  .main-nav__btn {
    display: none;
  }
}

.main-container__background {
  background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%);
  width: 100%;
  height: 100%;
}

.main-container__block {
  position: relative;
  background-color: #c4c4c4;
  color: #ffffff;
  background-image: url("../img/background/main-container-image.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .main-container__block {
    background-image: url("../img/background/main-container-image@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .main-container__block {
    background-image: url("../img/background/main-container-image-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .main-container__block {
    background-image: url("../img/background/main-container-image-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .main-container__block {
    background-image: url("../img/background/main-container-image-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .main-container__block {
    background-image: url("../img/background/main-container-image-mobile@2x.jpg");
  }
}

.main-container__wrapper {
  display: inline-block;
  margin-top: 25%;
  margin-left: 10%;
  padding-bottom: 23%;
}
@media (max-width: 1023px) {
  .main-container__wrapper {
    margin-left: 0;
    margin-top: 27%;
    padding-bottom: 31%;
  }
}
@media (max-width: 767px) {
  .main-container__wrapper {
    margin-top: 55%;
    padding-bottom: 71%;
  }
}
.main-container__wrapper h2 {
  margin: 0;
  padding: 0;
  font-family: "roboto-condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  margin-bottom: 32px;
  color: #f2f5f7;
}
@media (max-width: 1023px) {
  .main-container__wrapper h2 {
    font-size: 64px;
    line-height: 75px;
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .main-container__wrapper h2 {
    font-size: 36px;
    line-height: 42px;
    margin-bottom: 9px;
  }
}
.main-container__wrapper p {
  margin: 0;
  padding: 0;
  color: #f2f5f7;
  font-family: "pt-mono", "Arial", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 155%;
  max-width: 685px;
  margin-bottom: 47px;
}
@media (max-width: 1023px) {
  .main-container__wrapper p {
    margin-bottom: 56px;
  }
}
@media (max-width: 767px) {
  .main-container__wrapper p {
    font-size: 14px;
    margin-bottom: 45px;
  }
}
.main-container__wrapper .main-container__block__btn {
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  padding: 15px 25px;
  margin-left: 4px;
}
@media (max-width: 1023px) {
  .main-container__wrapper .main-container__block__btn {
    margin-left: 46px;
  }
}
@media (max-width: 767px) {
  .main-container__wrapper .main-container__block__btn {
    display: none;
  }
}

.main-container__block__btn-mobile {
  display: none;
}
@media (max-width: 767px) {
  .main-container__block__btn-mobile {
    display: block;
    padding: 11px 36px;
  }
}

.main-footer {
  background-color: #041827;
}

.main-footer__container {
  display: flex;
  padding-top: 50px;
  padding-bottom: 47px;
}
@media (max-width: 1023px) {
  .main-footer__container {
    flex-direction: column;
    padding-top: 50px;
    padding-bottom: 63px;
  }
}
@media (max-width: 767px) {
  .main-footer__container {
    padding-top: 35px;
    padding-bottom: 35px;
  }
}

.main-footer__logo {
  display: block;
  width: 215px;
  height: 55px;
  margin-bottom: 32px;
}
@media (max-width: 1023px) {
  .main-footer__logo {
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .main-footer__logo {
    margin-bottom: 14px;
  }
}

.main-footer__image {
  display: block;
  width: 100%;
  height: 100%;
}

.main-footer__top {
  margin-right: 13%;
}
@media (max-width: 1023px) {
  .main-footer__top {
    display: flex;
    justify-content: space-between;
    margin-right: 0;
    margin-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .main-footer__top {
    position: relative;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    padding-right: 5px;
    padding-bottom: 26px;
  }
  .main-footer__top::before {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
  }
}

.main-footer__scroll {
  display: flex;
  padding-top: 5px;
}
@media (max-width: 1023px) {
  .main-footer__scroll {
    justify-content: space-between;
    padding-top: 0;
  }
}
@media (max-width: 767px) {
  .main-footer__scroll {
    flex-direction: column;
  }
}

.social {
  display: grid;
  margin: 0;
  padding: 0 0 0 10px;
  list-style: none;
  gap: 15px;
  grid-template-columns: repeat(3, 40px);
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.link__container {
  display: block;
  margin-right: 96px;
}
@media (max-width: 1023px) {
  .link__container {
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .link__container {
    position: relative;
  }
  .link__container::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: -25px;
    width: 100vw;
    height: 1px;
    background-color: rgba(248, 253, 255, 0.7);
  }
}

@media (max-width: 767px) {
  .link__container[open] > summary:before {
    transform: rotate(0deg);
  }
}

.link__container summary::-webkit-details-marker {
  display: none;
}

.link__container summary::-moz-list-bullet {
  list-style-type: none;
}

.link__container summary::marker {
  display: none;
}

.link__container summary {
  display: inline-block;
  margin-bottom: 20px;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  font-family: "roboto-condensed", "Arial", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  cursor: pointer;
}
@media (max-width: 767px) {
  .link__container summary {
    position: relative;
    width: 100%;
    margin-bottom: 0;
    padding-top: 22px;
    padding-bottom: 22px;
  }
  .link__container summary::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
  }
  .link__container summary::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
    transform: rotate(90deg);
  }
}
.link__container summary:focus {
  outline: 0;
  box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.3), inset 0 0 2px rgba(0, 0, 0, 0.3);
}

