.main-container__background {
  background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%);
  width: 100%;
  height: 100%;
}

.main-container__block {
  position: relative;
  background-color: $color-silver;
  color: $color-default-white;
  background-image: url("../img/background/main-container-image.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/background/main-container-image@2x.jpg");
  }

  @include vp-1023 {
    background-image: url("../img/background/main-container-image-tablet.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/background/main-container-image-tablet@2x.jpg");
    }
  }

  @include vp-767 {
    background-image: url("../img/background/main-container-image-mobile.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/background/main-container-image-mobile@2x.jpg");
    }
  }
}

.main-container__wrapper {
  display: inline-block;
  margin-top: 25%;
  margin-left: 10%;
  padding-bottom: 23%;

  @include vp-1023 {
    margin-left: 0;
    margin-top: 27%;
    padding-bottom: 31%;
  }

  @include vp-767 {
    margin-top: 55%;
    padding-bottom: 71%;
  }

  h2 {
    margin: 0;
    padding: 0;
    font-family: $ff-roboto-condensed;
    font-style: normal;
    font-weight: 700;
    font-size: 80px;
    line-height: 94px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    margin-bottom: 32px;
    color: $color-alice-blue;

    @include vp-1023 {
      font-size: 64px;
      line-height: 75px;
      margin-bottom: 0;
    }

    @include vp-767 {
      font-size: 36px;
      line-height: 42px;
      margin-bottom: 9px;
    }
  }

  p {
    margin: 0;
    padding: 0;
    color: $color-alice-blue;
    font-family: $ff-ptmono;
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 155%;
    max-width: 685px;
    margin-bottom: 47px;

    @include vp-1023 {
      margin-bottom: 56px;
    }

    @include vp-767 {
      font-size: 14px;
      margin-bottom: 45px;
    }
  }

  .main-container__block__btn {
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    padding: 15px 25px;
    margin-left: 4px;

    @include vp-1023 {
      margin-left: 46px;
    }

    @include vp-767 {
      display: none;
    }
  }
}

.main-container__block__btn-mobile {
  display: none;

  @include vp-767 {
    display: block;
    padding: 11px 36px;
  }
}
